import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import config from '../../config'
import api from '../../api/index'

import '../../styles/index.css'

Vue.config.productionTip = false;

Vue.prototype.$config = config;
Vue.prototype.$api = api;

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app');
